<template>
  <div>
      <Header title="电影清单" ref="myswiper"></Header>
        <div id="content" :style="hidden" v-if="$route.path=='/movie/NowPlaying' || '/movie/ComingSoon'">
            <div id="movie_menu" :class="isFixed?'fixed': ''">
				<router-link tag="div" to="/movie/City" class="city_name">
					<span @click="goToCity">{{$store.state.city.cityName}} </span><i class="iconfont icon-lower-triangle"></i>
				</router-link>
				<div class="hot_swtich">
					<router-link tag="div" to="/movie/NowPlaying" class="hot_item ">正在热映</router-link>
					<router-link tag="div" to="/movie/ComingSoon" class="hot_item">即将上映</router-link>
				</div>
				<router-link tag="div" to="/movie/Search" class="search_entry">
					<van-icon class="iconfont icon-sousuo" name="search" />
				</router-link>
			</div>
            <!-- <keep-alive> -->
                <router-view></router-view>

        </div>
      <TabBar></TabBar>
      <!-- <router-view name="detail"></router-view> -->
  </div>
</template>

<script>

import Header from '@/components/Header'
import TabBar from '@/components/TabBar'

export default {
    name:'Movie',
    
    data() {
        return {
            isFixed:false,
            hidden:{
            display: 'block'
            },
        }
    },
    mounted() {
        // console.log(this.handleScroll);
        window.onscroll = this.handleScroll,
        this.$bus.$on('hidden')
    },
    beforeDestroy() {
        window.onscroll = null
    },
    methods: {
        goToCity() {
        this.$router.push('/city')
        },
        handleScroll(){
            // console.log(this.$router);
            // console.log(this.$store);
            // console.log(this.$bus.$on('hidden'));  .$store.state.cityName
            // console.log("1111");
            // console.log(document.documentElement.scrollTop,this.$refs.myswiper);
            // console.log(document.documentElement.scrollTop,this.$refs.myswiper.$el.offsetHeight);
            // if(滚动距离>=轮播高度)
            if (document.documentElement.scrollTop>=this.$refs.myswiper.$el.offsetHeight) {
                // console.log("fixed");
                this.isFixed = true
            }else{
                // console.log("unfixed");
                this.isFixed = false
            }
        },

    },
    components:{
        Header,
        TabBar
    }
}
</script>

<style>
#content #movie_menu{ width: 100%; height: 45px; border-bottom:1px solid #e6e6e6; display: flex; justify-content:space-between; align-items:center; background:white; z-index:10;}
#movie_menu .city_name{ margin-left: 20px; height:100%; line-height: 45px;}
#movie_menu .city_name.active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
#movie_menu .city_name.router-link-active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
#movie_menu .hot_swtich{ display: flex; height:100%; line-height: 45px;}
#movie_menu .hot_item{ font-size: 15px; color:#666; width:80px; text-align:center; margin:0 12px; font-weight:700;}
#movie_menu .hot_item.active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
#movie_menu .hot_item.router-link-active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
#movie_menu .search_entry.active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
#movie_menu .search_entry.router-link-active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
#movie_menu .search_entry i{  font-size:24px; color:red;}
.fixed{
    position:fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 40px;
}
</style>